import { useQuery } from "react-query";
import { useOutletContext } from "react-router-dom";
import styled from "styled-components";
import { getContentDetails, IGetTvDetail } from "../api";
import { makeImagePath } from "../util";

const Loader = styled.div`
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const BigCover = styled.div`
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
`;
const BigTitle = styled.h3`
    color: ${props => props.theme.white.lighter};
    font-size: 40px;
    position: relative;
    top: -80px;
    padding: 20px;
`;
const BigOverview = styled.p`
    padding: 20px;
    position: relative;
    top: -150px
    color: ${props => props.theme.white.lighter};
`;

interface IDetail {
    id: number;
    content: string;
}
function TvDetail() {
    const { id, content } = useOutletContext<IDetail>();
    const {data, isLoading} = useQuery<IGetTvDetail>(
        ["details", "content_detail"], 
        () => getContentDetails(id, content)
    );
    
    return (<>
        {isLoading ? <Loader>Loading...</Loader> : <>
            <BigCover style={{backgroundImage: `linear-gradient(to top, black, transparent), 
                url(${makeImagePath(data?.backdrop_path as string, "w500")})`}} />
            <BigTitle>{data?.name}</BigTitle>
            <BigOverview>{data?.overview}</BigOverview>
            <div>{data?.first_air_date}</div>
        </>}
    </>);
}

export default TvDetail;