import { AnimatePresence, motion, useScroll } from "framer-motion";
import { Outlet, useMatch, useNavigate } from "react-router-dom";
import { useRecoilValue } from "recoil";
import styled from "styled-components";
import { rootState } from "../atoms";


const Overlay = styled(motion.div)`
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
`;
const BigMovie = styled(motion.div)`
    position: absolute;
    width: 40vw;
    height: 80vh;
    left: 0;
    right: 0;
    margin: 0 auto;
    background-color: ${props => props.theme.black.lighter};
    border-radius: 15px;
    overflow: hidden;
`;

interface IModal {
    content: string;
}
function Modal({ content } : IModal) {

    const navigate = useNavigate();
    const getRoot = useRecoilValue(rootState);
    const bigContentMatch = useMatch(`/${content}/:id`);
    
    const paramId = bigContentMatch?.params.id;
    const id = paramId?.split('_')[0];
    const { scrollY } = useScroll();
    
    const onOverlayClicked = () => navigate(getRoot[content]);
    
    
    return (<>
    {bigContentMatch? (
        <AnimatePresence><>
            <Overlay 
                onClick={onOverlayClicked} 
                exit={{opacity: 0}} 
                animate={{opacity: 1}}
                key={content}/>
            <BigMovie 
                layoutId={paramId}
                style={{top: scrollY.get() + 100}} > 
                <Outlet context={{ id, content, }} />
            </BigMovie>
        </></AnimatePresence>
    ) : null}
    </>)
}

export default Modal;