const API_KEY = "75f6ac300ef95ee23a13b015f0ea6906";
const BASE_FETCH = "https://api.themoviedb.org/3";

interface IMovie {
    id: number;
    backdrop_path: string;
    poster_path: string;
    title: string;
    overview: string;
}

interface ITv {
    id: number;
    backdrop_path: string;
    first_air_date: string;
    original_name: string;
    overview: string;
}

export interface IGetMoviesResult {
    dates: {
        minimum: string;
        maximum: string;
    };
    page : number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
}

export interface IGetMovieDetail {
    id: number;
    title: string;
    backdrop_path : string;
    genres: string[];
    overview: string;
    release_date: string;
    runtime: number;
    adult: boolean;
    vote_average: number;
}

export interface IGetTvResult {
    results: ITv[];
    page : number;
    total_pages: number;
    total_results: number;
}

export interface IGetTvDetail {
    id: number;
    name: string;
    backdrop_path : string;
    genres: string[];
    overview: string;
    first_air_date: string;
    last_air_date: string;
    number_of_episodes: number;
    number_of_seasons: number;
    adult: boolean;
    vote_average: number;
}

export function getContentResults(keyName:string, content:string) {
    return fetch(`${BASE_FETCH}/${content}/${keyName}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
};

export function getContentDetails(contentId:number, content:string) {
    return fetch(`${BASE_FETCH}/${content}/${contentId}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
};

export function getNowPlayingMovies() {
    return fetch(`${BASE_FETCH}/movie/now_playing?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
};

