import { atom } from "recoil";

interface ISlider {
    keyName: string;
    title: string;
}

interface IRootUrl {
    [key: string] : string;
}

export const moviesState = atom<ISlider[]>({
    key: "movies",
    default: [
        {
            keyName: "now_playing",
            title: "Now Playing",
        },{
            keyName: "top_rated",
            title: "Top Rated Movies",
        },{
            keyName: "popular",
            title: "Popular Movies",
        },{
            keyName: "upcoming",
            title: "Upcoming Movies",
        },
    ],
});

export const tvState = atom<ISlider[]>({
    key: "tvs",
    default: [
        {
            keyName: "airing_today",
            title: "TV Airing Today",
        },{
            keyName: "top_rated",
            title: "Top Rated TV",
        },{
            keyName: "popular",
            title: "Popular Movies",
        },{
            keyName: "on_the_air",
            title: "TV On The Air",
        },
    ],
});

export const rootState = atom<IRootUrl>({
    key: "rootUrl",
    default: {
        movie: "/",
        tv: "/tv",
    },
});